import React from 'react';

const Dashboard = React.lazy(() => import('./views/dashboard/DashboardContainer'));
const Room = React.lazy(() => import('./views/rooms/Room'));
const Category = React.lazy(() => import("./views/categories/Category"));
const Item = React.lazy(() => import("./views/items/Item"));
const WalletHistory = React.lazy(() => import("./views/wallets/WalletHistory"));
const User = React.lazy(() => import("./views/users/User"));
const RoomDetail = React.lazy(() => import('./views/rooms/RoomDetail'));
const UserDetail = React.lazy(() => import('./views/users/UserDetail'));

// https://github.com/ReactTraining/react-router/tree/master/packages/react-router-config
const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/dashboard', name: 'Dashboard', component: Dashboard },
  { path: '/user', name: 'User', component: User },
  { path: '/room', name: 'Room', component: Room },
  { path: '/category', name: 'Category', component: Category },
  { path: '/item', name: 'Item', component: Item },
  { path: '/walletHistory', name: 'Wallet History', component: WalletHistory },
  { path: '/roomdetail', name: 'Room Detail', component: RoomDetail },
  { path: '/userdetail', name: 'User Detail', component: UserDetail },
];

export default routes;
